import React from 'react'
import {connect} from 'react-redux';
import {Button, Form, Header, Input, Sticky} from "semantic-ui-react";
import './index.css'

import eventEmitter from "../../commons/eventEmitter";

class PgsqlProvision extends React.Component {

    constructor(props) {
        super(props);
    }

    runScript(){
        eventEmitter.emit(
            'showCmdViewer',
            {
                cmdUrl:'ldlalacmdUrl'
            }
        )
    }

    render() {
        const {} = this.props
        return (
            <div className='playbook-item'>
                <div className='playbook-item-intro'>
                    <h1>Pgsql Provision</h1>
                    <div className="lead">How to define and create postgres clusters?</div>


                </div>


                <Form className='playbook-item-run-script' >
                    <Form.Field>
                        <label>Run Script</label>
                        <Input
                            action={{
                                color: 'primary',
                                content: 'Run',
                                onClick:this.runScript
                            }}
                            label='./pgsql.yml'
                            actionPosition='right'
                            placeholder='--tags=' />
                        <Form.Field>
                            <Form.Select
                                fluid
                                label='pg_role'
                                options={[
                                    { key: 'p', text: 'primary', value: 'primary' },
                                    { key: 'r', text: 'replica', value: 'replica' },
                                    { key: 'o', text: 'offline', value: 'offline' }
                                ]}
                                name='pg_role'
                                placeholder=''
                                value={''}
                                onChange={(e,{name,value})=>{
                                    this._inputHandle(name,value)
                                }}
                            />
                        </Form.Field>
                    </Form.Field>
                </Form>


            </div>

        );
    }
}

const mapStateToProps = (state, props) => ({

});
const mapDispatchToProps = dispatch => ({});

export default connect(mapStateToProps, mapDispatchToProps)(PgsqlProvision);
