import React from 'react';
export default class NoResultTips extends React.Component {
    render() {
        const {
            title,
            desc,
            buttonText,
            buttomCallback
        } = this.props
        return (
            <div className="empty">
                <p className="empty-title h5">{title?title:'nothing'}</p>
                {desc?(
                    <p className="empty-subtitle font-lighter">{desc}</p>
                ):null}

                {typeof buttomCallback == "function"?(
                    <div className="empty-action">
                        <button className="btn btn-lg btn-block btn-success">{buttonText?buttonText:'refresh'}</button>
                    </div>
                ):null}

            </div>
        );
    }
}

