import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {Button, Card, Checkbox, Form, Icon, Input, Label, List, Modal} from "semantic-ui-react";
import eventEmitter from "../../commons/eventEmitter";
import actions from "../../actions";

class EditClusterPgUserForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            editType: 'add',//add || edit
            name: '',
            password: '',
            roles: ['dbrole_offline'],
            expire_in: 365,
            pgbouncer: false,
            comment: '',
            clusterData: null
        }
    }

    componentDidMount() {
        this.emitter = eventEmitter.addListener('showEditClusterPgUserForm', (data) => {
            this.setState({
                open: true,
                ...data
            })
        })
    }

    setOpen(flag) {
        this.setState({
            open: flag
        })
    }

    _inputHandle(name, value) {
        this.setState({
            [name]: value
        })
    }

    _checkboxHandle(name, value, flag) {

        if(flag){
            this.setState({
                [name]:[...this.state[name],value]
            })
        }else {
            this.state[name].splice(this.state[name].findIndex(item => item === value), 1)
            this.setState({
                [name]:this.state[name]
            })
        }
    }

    _editClusterPgUser() {

    }

    _addNewClusterPgUser(){
        const {
            addNewClusterPgUser
        } = this.props;
        const {
            name,
            password,
            roles,
            expire_in,
            pgbouncer,
            comment,
            clusterName
        } = this.state;

        //
        addNewClusterPgUser({
            name,
            password,
            roles,
            expire_in,
            pgbouncer,
            comment,
            clusterName
        },backData=>{

        })
    }

    _removeClusterPgUser(name){
        const {
            removeClusterPgUser
        } = this.props;

        const {
            clusterName
        } = this.state;

        eventEmitter.emit(
            'showConfirmBox',
            {
                content:'Are you sure ?',
                callback:(flag)=>{
                    if(flag){
                        removeClusterPgUser({
                            name,
                            clusterName
                        },backData=>{

                        })
                    }
                }
            }
        )


    }

    _renderPgUserInfo(userInfo){
        let descs = [];
        if(userInfo){
            descs = userInfo.map((hKeyItem,hKeyIndex)=>{
                return (
                    <Label key={'k-'+hKeyIndex}>
                        {hKeyItem}:
                        <Label.Detail>{userInfo[hKeyItem].toString()}</Label.Detail>
                    </Label>
                )
            })
        }
    }

    _renderPgUsers() {
        const {
            clusterName
        } = this.state;
        const {
            configData
        } = this.props;
        let clusterData = configData.all.children[clusterName]

        let renders = [];
        if (clusterData?.vars?.pg_users) {
            renders = clusterData.vars.pg_users.map((pItem, pIndex) => {
                return (
                    <List.Item className='mb-1rem'>
                        <Button size='mini' icon='trash' basic className='right floated no-shadow'
                            onClick={this._removeClusterPgUser.bind(this,pItem.name)}
                        />
                        <Icon
                            name='user'
                            className='left floated'
                            fitted
                            size='large'
                        />
                        <List.Content>
                            <List.Header as='a'>{pItem.name}</List.Header>
                            <List.Description className='mt-5px'>
                                {pItem.comment}
                            </List.Description>
                        </List.Content>
                    </List.Item>
                )
            })
        }


        return <List>
            {renders}
        </List>;
    }

    render() {
        const {
            open,
            editType,
            name,
            password,
            roles,
            expire_in,
            pgbouncer,
            comment
        } = this.state;
        return (
            <Modal
                onClose={this.setOpen.bind(this, false)}
                open={open}
                size='small'
            >
                <Modal.Header>{'Manage Cluster Pg Users'}</Modal.Header>
                <Modal.Content>
                    {this._renderPgUsers()}
                    <Card fluid>
                        <Card.Content>
                            <Form>
                                <Form.Group widths='equal'>
                                    <Form.Field>
                                        <label>name</label>
                                        <Input
                                            placeholder=''
                                            name='name'
                                            value={name}
                                            onChange={(e, {name, value}) => {
                                                this._inputHandle(name, value)
                                            }}
                                        />
                                    </Form.Field>
                                    <Form.Field>
                                        <label>password</label>
                                        <Input
                                            placeholder=''
                                            name='password'
                                            value={password}
                                            onChange={(e, {name, value}) => {
                                                this._inputHandle(name, value)
                                            }}
                                        />
                                    </Form.Field>
                                </Form.Group>

                                <Form.Group inline>
                                    <label>pgbouncer</label>
                                    <Form.Radio
                                        label='true'
                                        name='pgbouncer'
                                        checked={pgbouncer === true}
                                        onChange={(e, {name, value}) => {
                                            this._inputHandle(name, true)
                                        }}
                                    />
                                    <Form.Radio
                                        label='false'
                                        name='pgbouncer'
                                        checked={pgbouncer === false}
                                        onChange={(e, {name, value}) => {
                                            this._inputHandle(name, false)
                                        }}
                                    />
                                </Form.Group>

                                <Form.Group inline>
                                    <label>roles</label>
                                    <div className='flex-row-left flex-wrap'>
                                        <Form.Field>
                                            <Form.Checkbox label='dbrole_readonly'
                                                           name='dbrole_readonly'
                                                           checked={(roles.find(val => val === 'dbrole_readonly') !== undefined)}
                                                           onChange={(e,{ checked }) => {
                                                               this._checkboxHandle('roles', 'dbrole_readonly',checked)
                                                           }}
                                            />
                                        </Form.Field>
                                        <Form.Field>
                                            <Form.Checkbox label='dbrole_offline'
                                                           name='dbrole_offline'
                                                           checked={(roles.find(val => val === 'dbrole_offline') !== undefined)}
                                                           onChange={(e,{ checked }) => {
                                                               this._checkboxHandle('roles', 'dbrole_offline',checked)
                                                           }}
                                            />
                                        </Form.Field>
                                        <Form.Field>
                                            <Form.Checkbox label='dbrole_readwrite'
                                                           name='dbrole_readwrite'
                                                           checked={roles.find(val => val === 'dbrole_readwrite') !== undefined}
                                                           onChange={(e,{ checked }) => {
                                                               this._checkboxHandle('roles', 'dbrole_readwrite',checked)
                                                           }}
                                            />
                                        </Form.Field>
                                        <Form.Field>
                                            <Form.Checkbox label='dbrole_admin'
                                                           name='dbrole_admin'
                                                           checked={roles.find(val => val === 'dbrole_admin') !== undefined}
                                                           onChange={(e,{ checked }) => {
                                                               this._checkboxHandle('roles', 'dbrole_admin',checked)
                                                           }}
                                            />
                                        </Form.Field>
                                    </div>

                                </Form.Group>

                                <Form.Field>
                                    <label>comment</label>
                                    <Input
                                        placeholder='example personal user for interactive queries'
                                        name='comment'
                                        value={comment}
                                        onChange={(e, {name, value}) => {
                                            this._inputHandle(name, value)
                                        }}
                                    />
                                </Form.Field>
                            </Form>
                            <Card.Content extra className='flex-row-center mt-1rem'>
                                <Button
                                    primary
                                    onClick={this._addNewClusterPgUser.bind(this)}
                                >Add</Button>
                            </Card.Content>
                        </Card.Content>
                    </Card>
                </Modal.Content>
                <Modal.Actions>
                    <Button
                        onClick={this.setOpen.bind(this, false)}
                    >Close</Button>
                </Modal.Actions>
            </Modal>

        )
    }
}

const mapStateToProps = (state, props) => ({
    configData:state.configData
});
const mapDispatchToProps = dispatch => ({
    addNewClusterPgUser: (formParams, callBack) => dispatch(actions.addNewClusterPgUser(formParams, callBack)),
    removeClusterPgUser: (formParams, callBack) => dispatch(actions.removeClusterPgUser(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EditClusterPgUserForm));
