import React from 'react'
import {connect} from 'react-redux';
import {Button, Form, Header, Input, Sticky} from "semantic-ui-react";
import './index.css'

import eventEmitter from "../../commons/eventEmitter";

class MonitorOnly extends React.Component {

    constructor(props) {
        super(props);
    }

    runScript(){
        eventEmitter.emit(
            'showCmdViewer',
            {
                cmdUrl:'ldlalacmdUrl'
            }
        )
    }

    render() {
        const {} = this.props
        return (
            <div className='playbook-item'>
                <div className='playbook-item-intro'>
                    <h1>Monitor Only</h1>
                    <div className="lead">How to deploy pigsty monitoring system without provisioning solution?</div>

                </div>


                <Form className='playbook-item-run-script' >
                    <Form.Field>
                        <label>Run Script</label>
                        <Input
                            action={{
                                color: 'primary',
                                content: 'Run',
                                onClick:this.runScript
                            }}
                            label='./pgsql-monitor.yml'
                            actionPosition='right'
                            placeholder='-l' />
                    </Form.Field>
                </Form>


            </div>

        );
    }
}

const mapStateToProps = (state, props) => ({});
const mapDispatchToProps = dispatch => ({});

export default connect(mapStateToProps, mapDispatchToProps)(MonitorOnly);
