import Axios from "axios";
import qs from 'qs';
import Config from '../config';
import ActionTypes from './types';
import YAML from 'yaml'
//
export function getConfigData(formParams,callBack) {
    return dispatch=>{
        Axios({
            method:'get',
            url:Config.officialServerUrl+'/api/v1/config',
            data:qs.stringify({
                ...formParams
            })
        }).then(data=>{
            let jsonYaml;
            if(typeof(data.data) == 'object'){
                jsonYaml = data.data;
            }else {
                jsonYaml = YAML.parse(data.data);
            }
            //console.log(jsonYaml);
            if(jsonYaml.hasOwnProperty('all')){
                dispatch({
                    type:ActionTypes.GET_CONFIG_DATA_SUCCESS,
                    storeName:'configData',
                    data:jsonYaml,
                    ...formParams
                });
                if (typeof callBack === 'function') {
                    callBack({
                        success:true,
                        data:jsonYaml,
                    })
                }
            }else {
                if (typeof callBack === 'function') {
                    callBack({
                        success:false,
                        error:data.data
                    })
                }
            }
        }).catch(error=>{
            //debugger;
        })
    }
}

