export function saveLocalConfigData(formParams,callBack) {
    return dispatch=>{
        dispatch({
            type:ActionTypes.SAVE_LOCAL_CONFIG_DATA_SUCCESS,
            storeName:'configData',
            ...formParams
        });
        if (typeof callBack === 'function') {
            callBack({
                success:true,
            })
        }
    }
}

