import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import actions from "../../actions";
import { Button, Card,Icon,List } from 'semantic-ui-react';
import ClusterHostItem from "./clusterHostItem";
import NoResultTips from "../../components/noResultTips";
import eventEmitter from "../../commons/eventEmitter";
import Config from "../../config";

const styles = {
    clusterIcon:{
        fontSize:'3rem'
    }
}

class ClusterItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }

    _renderHosts(){
        const {
            clusterData,
            clusterName
        } = this.props;

        let renders = [];
        if(clusterData?.hosts){
            renders = Object.keys(clusterData?.hosts).map((cItem,cIndex)=>{
                return <ClusterHostItem
                    key={'key-'+cIndex}
                    clusterName={clusterName}
                    hostName={cItem}
                    hostData={clusterData.hosts[cItem]}
                />
            });
        }

        if(renders.length === 0){
            return <NoResultTips title={'no hosts'}/>
        }

        return renders;

    }


    _addNewClusterHost(){
        const {
            clusterName
        } = this.props;
        eventEmitter.emit(
            'showEditClusterHostForm',
            {
                editType:'add',
                clusterName,
                callback:(flag)=>{
                    console.log(flag)
                }
            }
        )
    }

    _manageClusterPgUser(){
        const {
            clusterName,
            clusterData
        } = this.props;
        eventEmitter.emit(
            'showEditClusterPgUserForm',
            {
                clusterName,
                clusterData,
                callback:(flag)=>{
                    console.log(flag)
                }
            }
        )
    }

    _runScript(){
        const {
            clusterData,
            clusterName
        } = this.props;
        eventEmitter.emit(
            'showConfirmBox',
            {
                content:'Are you sure run this script ?',
                confirmButton:'Run',
                callback:(flag)=>{
                    if(flag){
                        eventEmitter.emit(
                            'showCmdViewer',
                            {
                                sseUrl:Config.officialServerUrl+'/api/v1/pgsql/'+clusterName+'/init',
                                clusterName:clusterName
                            }
                        )
                    }
                }
            }
        )

    }

    _removeCluster(){
        const {
            removeCluster,
            clusterData,
            clusterName
        } = this.props;

        eventEmitter.emit(
            'showConfirmBox',
            {
                content:'It will remove this cluster !',
                callback:(flag)=>{
                    if(flag){
                        eventEmitter.emit(
                            'showCmdViewer',
                            {
                                sseUrl:Config.officialServerUrl+'/api/v1/pgsql/'+clusterName+'/remove',
                                clusterName:clusterName
                            }
                        )
                    }
                }
            }
        )
    }

    render() {
        const {
            clusterData,
            clusterName
        } = this.props;
        let ownHostsCount = 0 ;
        if(clusterData.hasOwnProperty('hosts')){
            ownHostsCount = Object.keys(clusterData.hosts).length;
        }
        let ownPgUsersCount = 0;
        if(clusterData?.vars?.pg_users){
            ownPgUsersCount = clusterData?.vars?.pg_users.length;
        }
        return (
            <Card fluid>
                <Card.Content>
                    <Icon
                        name='server'
                        className='left floated mr-1rem'
                        size='large'
                    />
                    <div className='right floated'>
                        <Button size='mini'
                            onClick={this._addNewClusterHost.bind(this)}
                        ><Icon name='plus' />Add Host</Button>
                        <Button size='mini'
                            onClick={this._manageClusterPgUser.bind(this)}
                        ><Icon name='setting' />Pg Users</Button>
                    </div>

                    <Card.Header>{clusterName}</Card.Header>
                    <Card.Meta>{ownHostsCount} Hosts | {ownPgUsersCount} PgUsers</Card.Meta>
                    <List>
                        {this._renderHosts()}
                    </List>
                </Card.Content>
                <Card.Content extra>
                    <div className='flex-row-right'>
                        <Button color={clusterData.edit?'orange':'grey'}
                            onClick={this._runScript.bind(this)}
                        >
                            Run Script
                        </Button>
                        <Button icon='trash'
                            onClick={this._removeCluster.bind(this)}
                        />
                    </div>
                </Card.Content>
            </Card>
        )
    }
}

const mapStateToProps = (state, props) => ({

});
const mapDispatchToProps = dispatch => ({
    addNewClusterHost: (formParams, callBack) => dispatch(actions.addNewClusterHost(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ClusterItem));
